.card {
    width: 350px;
    margin: 0.5em;
    cursor: pointer;
    background: black;
    border: 1px solid rgb(19, 243, 228);
}

.image {
    border-radius: 5px;
    transition: all 0.25s;
}

.image:hover {
    transform: scale(1.05);
    border: none;
}

@media only screen and (max-width: 960px) {
    .card {
        width: 100%;
    }
}

.card-inner {
    position: relative;
    transition: transform 0.5s;
    transform-style: preserve-3d;
}
  
.flip .card-inner {
    transform: rotateX(180deg);
}
  